.slidercard {
  min-width: 500px;
  height: 100%;
  display: flex;
  position: relative;
  flex: 1;
  opacity: 0.5;
  transform: scale(0.7);
  transition: opacity 300ms linear,
    transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955),
    box-shadow 300ms ease-in-out;
  box-shadow: none;

  .slidercard__content {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
