.similarsongslistelement {
  width: 100%;
  height: 40px;
  display: flex;

  align-items: center;
  cursor: pointer;

  h4 {
    font-weight: 700;
    font-size: 1rem;
    letter-spacing: 2px;
    margin: 0.7rem 0 0.7rem 1rem;
  }
  p {
    margin: 0.7rem 1rem 0.7rem 0;
    font-size: 0.9rem;
  }

  .similarsongslistelement__column-left {
    width: 85%;
  }
  .similarsongslistelement__column-right {
    width: 15%;
  }
}
