$border: 1px solid lightgray;
@mixin for-s-tablet-down {
  @media (max-width: 720px) {
    @content;
  }
}

@mixin for-l-tablet-down {
  @media (max-width: 1024px) {
    @content;
  }
}

.similarsongslist {
  width: 50%;
  display: flex;
  flex-direction: column;
  @include for-s-tablet-down {
    width: 100%;
  }
  &.similarsongslist-row {
    width: 70%;
    h2 {
      background-color: #d794c1;
      margin: 0 auto 1rem auto;
    }
    @include for-s-tablet-down {
      width: 100%;
    }
    @include for-l-tablet-down {
      width: 90%;
    }
  }

  .similarsongslist__columns {
    display: flex;
    flex-direction: row;

    .similarsongslist__column-left {
      width: calc(85% - 5px);
      h5 {
        margin: 0 0 1rem 1rem;
      }
    }
    .similarsongslist__column-right {
      width: calc(15% + 5px);

      h5 {
        margin: 0 1rem 1rem 0;
      }
    }
  }

  .similarsongslist__scroll {
    overflow-x: hidden;
    overflow-y: scroll;
    width: 100%;
    height: 25vh;
    border-bottom: $border;
    border-top: $border;
  }

  h2 {
    font-size: 3rem;
    text-align: center;
    margin: 0 0 1rem 0;
  }
}
